.detail-page {
  padding-top: 50px;
}
.detail-page .navigation-wrapper {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 100;
  height: 50px;
}
.detail-page .detail-wrapper {
  padding: 15px;
}
.detail-page .detail-wrapper .summary-content {
  height: 5em;
  overflow: hidden;
  text-indent: 2rem;
  line-height: 26px;
}
.detail-page .detail-wrapper .summary-content.full {
  height: auto;
}
.detail-page .detail-wrapper .card-content {
  display: -ms-flexbox;
  display: flex;
  overflow-x: scroll;
}
.detail-page .detail-wrapper .card {
  width: 50%;
  min-width: 150px;
  max-width: 250px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  text-align: center;
  padding: 10px 6px 15px 6px;
}
.detail-page .detail-wrapper .card.video {
  min-width: 250px;
}
.detail-page .detail-wrapper .card-img,
.detail-page .detail-wrapper .card-video {
  max-width: 100%;
}
.detail-page .detail-item__btn {
  color: #0e88eb;
  margin: 10px auto;
  display: block;
  width: 100%;
  text-align: center;
}
.detail-page .detail-item .detail-item__images {
  display: -ms-flexbox;
  display: flex;
  overflow-x: scroll;
}
.detail-page .detail-item .detail-item__images .video-container {
  min-width: 250px;
}
.detail-page .detail-item .detail-item__images img,
.detail-page .detail-item .detail-item__images video {
  max-width: 100%;
}
.detail-page .detail-item .detail-item__images .role {
  color: #666;
}



